import neilModal from '../../components/neil-modal/neil-modal.vue';
import { request } from '../../utils/request.js';
import uniSwiperdot from '../../components/uni-swiper-dot/uni-swiper-dot.vue';
import uniSection from '../../components/uni-section/uni-section.vue';
export default {
	components: {
		neilModal,
		uniSwiperdot,
		uniSection
	},
	data() {
		return {
			//  用户协议框的显示隐藏
			show2: false,
			// 获取到的用户信息
			userInfo: {},
			// show1为false时，代表用户拒绝授权，此时显示重新授权按钮
			show1: true,
			// 轮播图数据
			info: [
				{
					url: 'https://ww1.yunjiexi.club/2020/03/21/bW0EL.png',
					content: '1'
				},
				{
					url: 'https://ww1.yunjiexi.club/2020/03/21/bPi3b.png',
					content: '2'
				},
				{
					url: 'https://ww1.yunjiexi.club/2020/03/21/bPx79.png',
					content: '3'
				}
			],
			dotsStyles: {
				backgroundColor: 'rgba(83, 200, 249,0.3)',
				border: '1px rgba(83, 200, 249,0.3) solid',
				color: '#fff',
				selectedBackgroundColor: 'rgba(83, 200, 249,0.9)',
				selectedBorder: '1px rgba(83, 200, 249,0.9) solid',
				bottom: 100
			},
			current: 0
		};
	},
	methods: {
		// 切换轮播图图片
		change(e) {
			this.current = e.detail.current;
		},
		// 二次授权后的回调函数
		reauthorize(e) {
			if (e.detail.authSetting['scope.userInfo']) {
				// 若二次授权成功，切换对话框的显示按钮
				this.show1 = true;
			}
		},
		// 调用用户协议框
		modalTap(){
			this.show2 = true;
		},
		// 获取用户信息，如果获取失败跳转到登录页面
		getUserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: result => {
					this.userInfo = result.userInfo;
				},
				fail: error => {
					let content = error.errMsg;
					if (~content.indexOf('uni.login')) {
						content = '请在登录页面完成登录操作';
						uni.reLaunch({
							url: '/pages/authorise/authorise'
						});
					}
				}
			});
		},
		// 获取到用户信息后，调用登录接口，如果被拒绝授权，就跳转到设置页面
		mpGetUserInfo(result) {
			if (result.detail.errMsg !== 'getUserInfo:ok') {
				this.show1 = false;
				return;
			}
			this.userInfo = result.detail.userInfo;
			const that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					// uni.setStorageSync('code', loginRes.code);
					that.userInfo.code = loginRes.code;
					that.userInfo.headimage = that.userInfo.avatarUrl
					// 优化过得代码
					request({
						url: '/api/user/MiniProgramLogin',
						data: that.userInfo,
						method: 'POST'
					}).then(res => {
						// 登录失败，提示错误信息,重新打开授权页面
						if (res.data.code != 1) {
							uni.showModal({
								title: res.data.errMsg,
								showCancel: false
							});
							uni.reLaunch({
								url: '/pages/authorise/authorise'
							});
							// 登陆成功
						} else {
							// 同样 展示登录信息
							uni.showModal({
								title: res.data.errMsg,
								showCancel: false
							});
							// 保存登陆成功获取的token
							uni.setStorageSync('token', res.data.data.userinfo.token);
							// 保存返回的被处理过的用户信息
							uni.setStorageSync('login', res.data.data.userinfo);
							// 登陆成功 跳转到tab首页
							uni.switchTab({
								url: '/pages/anchor/anchor'
							});
						}
					});
				}
			});
		}
	}
};
